import { HttpClient, HttpHeaders, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AppConfig } from '../appConfig';
import { ToastService } from './toast.service';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  webRootPath = AppConfig.webRootPath;//
  public isLoadingData = false;//
  constructor(
    public http: HttpClient,
    public toastService: ToastService
  ) {
    console.log('Hello HttpServiceProvider Provider');
  }

  //request
  public request(method: string = 'get', url: string, successFn, data: any = null, responseType: any = 'json') {

    this.http.request(
      method,
      url,
      {
        params: data,
        responseType: responseType,
      }
    ).subscribe(
      //
      successFn,
      //
      err => {
        //
        // this.toastServiceProvider.hideLoding();
        console.log("http");
        console.log(err);

        this.toastService.showMessage("error", "");
      },
      //
      () => {
        //
        // this.toastServiceProvider.hideLoding();
        console.log("http");
      }
    );
  }

  // post
  public post(url: string, data: any, successFn, errorFn = this.errorFn) {
    // this.isLoadingData = true;//
    // var authKey = window.localStorage.getItem("authKey") ? JSON.parse(window.localStorage.getItem("authKey")) : '';
    // var sessionId = window.localStorage.getItem("sessionId") ? JSON.parse(window.localStorage.getItem("sessionId")) : '';
    // var userInfo = window.localStorage.getItem("userinfo") ? JSON.parse(window.localStorage.getItem("userinfo")) : '';
    const header = new HttpHeaders({
      // 'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      'Content-Type': 'application/json;charset=utf-8',
      // 'authKey': authKey,
      // 'sessionId': sessionId,
      // 'userInfo': userInfo
    });
    this.http.post(
      // "http://haha.cc",
      this.webRootPath + url,
      // this.getWwwFormData(data),
      // JSON.stringify(data),
      data,
      {
        headers: header,
        // params: data,
      }
    ).subscribe(
      //
      successFn,
      //
      errorFn,
      //
      () => {
        // this.isLoadingData = false;//
        console.log("http");
      }
    );
  }
  errorFn = (err) => {
    // this.isLoadingData = false;//
    console.log("http");
    console.log(err);
    this.toastService.showMessage("error", ",");
  }
  public postOss(url: string, data: any, successFn, errorFn = this.ossErrorFn) {
    const req = new HttpRequest('POST', url, data, {
      // reportProgress: true
    });
    this.http.request(req)
      .subscribe(
        //
        successFn,
        //
        errorFn,
        //
        () => {
          //
          // this.toastServiceProvider.hideLoding();
          console.log("http");
        }
      );
  }
  ossErrorFn = (err) => {
    // this.isLoadingData = false;//
    console.log("http");
    console.log(err);
    // this.toastService.showMessage("error", "");
  }



  //x-www-form-urlencoded
  private getWwwFormData(data) {
    if (data == null) {
      return null;
    }
    let dataStr = "";
    for (var key in data) {
      dataStr += key + "=" + data[key] + "&";
    }
    dataStr = dataStr.substring(0, dataStr.length - 1);
    return dataStr;
  }
}

