import { Component, OnInit } from '@angular/core';
import * as Swiper from 'swiper';
@Component({
  selector: 'app-adswiper',
  templateUrl: './adswiper.component.html',
  styleUrls: ['./adswiper.component.css']
})
export class AdswiperComponent implements OnInit {

  constructor() { }
  adSwiper: any;//swiper
  ngOnInit() {


  }
  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    this.initAdSwiper();
    // console.log(this.adSwiper)
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    console.log("swiper destroy");
    this.adSwiper.destroy(true, true);
  }
  //swiper
  initAdSwiper() {
    this.adSwiper = new Swiper('#ad-swiper', {
      autoplay: 4000,//
      effect: "slide",//
      loop: true,//
      pagination: '#ad-pagination',//html
      // paginationType :'bullets',//
      paginationClickable: true,//
      prevButton: '.swiper-button-prev',
      nextButton: '.swiper-button-next',
    });
  }

}
